import { Component, OnInit } from '@angular/core';
import { SpMainService } from '../sp-main.service';
import { Technology, FrameWork } from '../sp-model';

@Component({
  selector: 'app-sp-skills',
  templateUrl: './sp-skills.component.html',
  styleUrls: ['./sp-skills.component.scss']
})
export class SpSkillsComponent implements OnInit {

  spSkills : Partial<Technology | FrameWork>[] = [];

  constructor(
    private spMainService : SpMainService,
  ) { }

  ngOnInit(): void {
    this.spMainService.getSPSkills().subscribe( ( data : Partial<Technology | FrameWork>[] ) => this.spSkills = data );
  }

}
