import { Component, OnInit, Input } from '@angular/core';
import { SpMainService } from '../sp-main.service';
import { SPSocialMedia } from '../sp-model';

@Component({
  selector: 'app-sp-social-media',
  templateUrl: './sp-social-media.component.html',
  styleUrls: ['./sp-social-media.component.scss']
})
export class SpSocialMediaComponent implements OnInit {

  @Input() updateSocialMedia = false;
  spSocialMedia : SPSocialMedia[] = [];

  constructor(
    private spMainService : SpMainService
  ) { }

  ngOnInit(): void {
    this.spMainService.getSocialMediaLinks().subscribe( ( data : SPSocialMedia[] ) => this.spSocialMedia = data );
  }

  toggleBackgroundColor(media : SPSocialMedia) {
    media.backgroundColor = !media.backgroundColor;
  }

}
