import { Component, OnInit } from '@angular/core';
import { fromEvent, Subscription, Observable } from 'rxjs';

@Component({
  selector: 'app-sp-scroller',
  templateUrl: './sp-scroller.component.html',
  styleUrls: ['./sp-scroller.component.scss']
})
export class SpScrollerComponent implements OnInit {

  showScroller: boolean = false;

  constructor() { }

  ngOnInit(): void {
    this.subscribeToWindowScroll();
  }

  subscribeToWindowScroll() {
    fromEvent(window, 'scroll')
      .subscribe(event => {
        if ((event.target as Document).scrollingElement.scrollTop > 45) {
          this.showScroller = true;
        } else {
          this.showScroller = false;
        }
      });
  }

  goToTop() {
    window.scrollTo({ top: 0, behavior: 'smooth' });
  }

}
