import { Injectable } from '@angular/core';
import { SPMenu, SPExperience, SPProject, FrameWork, Technology, SPSocialMedia } from './sp-model';
import { spMenu, spExperience, spProjects, spSkills, spSocialMedia } from './sp-data-model';
import { of, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SpMainService {

  constructor() { }

  getSPMenu() : Observable<SPMenu[]> {
    return of( spMenu );
  }

  getSPExperience() : Observable<SPExperience[]> {
    return of( spExperience );
  }

  getSPProjects() : Observable<SPProject[]> {
    return of( spProjects );
  }

  getSPSkills() : Observable<Partial<Technology | FrameWork>[]> {
    return of( spSkills );
  }

  getSocialMediaLinks() : Observable<SPSocialMedia[]> {
    return of( spSocialMedia );
  }
}
