import { Component, OnInit, HostListener, Input } from '@angular/core';
import { SPMenu } from '../sp-model';
import { SpMainService } from '../sp-main.service';

@Component({
  selector: 'app-sp-nav',
  templateUrl: './sp-nav.component.html',
  styleUrls: ['./sp-nav.component.scss']
})
export class SpNavComponent implements OnInit {

  @Input() title : string;
  animateHeader : boolean = false;
  spMenu : SPMenu[];

  constructor(
    private spMainService : SpMainService,
  ) { }

  ngOnInit(): void {
    this.spMainService.getSPMenu().subscribe( ( data : SPMenu[] ) => this.spMenu = data );
  }

  @HostListener( 'window:scroll', [ '$event' ] ) onscroll( event ) {
    if( ( event.target as Document ).scrollingElement.scrollTop > 45 ) {
      this.animateHeader = true;
    } else {
      this.animateHeader = false;
    }
  }

}
