import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SpHomeComponent } from './sp-home/sp-home.component';
import { SpExperienceComponent } from './sp-experience/sp-experience.component';
import { SpProjectsComponent } from './sp-projects/sp-projects.component';
import { SpAboutMeComponent } from './sp-about-me/sp-about-me.component';
import { SpContactMeComponent } from './sp-contact-me/sp-contact-me.component';
import { SpSkillsComponent } from './sp-skills/sp-skills.component';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';


const routes: Routes = [
  {
    path : 'about-me',
    component : SpAboutMeComponent,
  }, {
    path : '',
    redirectTo : '/about-me',
    pathMatch : 'full'
  }, {
    path : 'experience',
    component : SpExperienceComponent,
  }, {
    path : 'projects',
    component : SpProjectsComponent,
  }, {
    path : 'skills',
    component : SpSkillsComponent,
  }, {
    path : 'contact-me',
    component : SpContactMeComponent,
  }, {
    path : '**',
    component : PageNotFoundComponent,
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash : true, scrollPositionRestoration : 'enabled' })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
