import { Component, OnInit } from '@angular/core';
import { SpMainService } from '../sp-main.service';
import { SPExperience } from '../sp-model';

@Component({
  selector: 'app-sp-experience',
  templateUrl: './sp-experience.component.html',
  styleUrls: ['./sp-experience.component.scss']
})
export class SpExperienceComponent implements OnInit {

  spExperience : SPExperience[];

  constructor(
    private spMainService : SpMainService,
  ) { }

  ngOnInit(): void {
    this.spMainService.getSPExperience().subscribe( ( data : SPExperience[] ) => this.spExperience = data );
  }

}
