import { Component, OnInit, Input, ViewChild, ElementRef } from '@angular/core';

@Component({
  selector: 'app-sp-carousel',
  templateUrl: './sp-carousel.component.html',
  styleUrls: ['./sp-carousel.component.scss']
})
export class SpCarouselComponent implements OnInit {

  private _paras : { left : number; para : string }[] = [];

  @ViewChild( 'rightArrow', { static : true } ) rightArrow : ElementRef;

  @Input()
  get paras() {
    return this._paras;
  }
  set paras( paragraphs : any[] ) {
    this._paras = paragraphs.map( ( p : string, i : number ) => !i ? { left : 0, para : p } : { left : 100, para : p } );
  }

  currentIndex : number = 0;
  leftDisabled : boolean = true;
  rightDisabled : boolean = false;

  constructor() { }

  ngOnInit(): void {
    const rightBtnElement : HTMLButtonElement = this.rightArrow.nativeElement;
    // setInterval( () => {
    //     if( !this.rightDisabled ) {
    //       rightBtnElement.click();
    //     } else {
    //       this.currentIndex = 0;
    //       this.rightDisabled = false;
    //       this.leftDisabled = true;
    //       this.paras.forEach( ( para, index ) => {
    //         if( index ) {
    //           para.left = 100;
    //         } else {
    //           para.left = 0;
    //         }
    //       } );
    //     }
    //  }, 6500 );
  }

  updatePara( right : boolean ) {
    const ci = this.currentIndex;
    const parasLength : number = this.paras.length;
    if( right ) {
      if( this.currentIndex + 1 < parasLength ) {
        this.currentIndex += 1;
        this.paras[ ci ].left = -100;
        this.paras[ this.currentIndex ].left = 0;
        this.leftDisabled = false;
        if( this.currentIndex + 1 === parasLength ) {
          this.rightDisabled = true;
        }
      }
    } else {
      if( this.currentIndex > 0 ) {
        this.currentIndex -= 1;
        this.paras[ ci ].left = 100;
        this.paras[ this.currentIndex ].left = 0;
        this.rightDisabled = false;
        if( this.currentIndex === 0 ) {
          this.leftDisabled = true;
        }
      }
    }
  }

}
