import { Component, VERSION } from '@angular/core';
import { SpContactMeComponent } from './sp-contact-me/sp-contact-me.component';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  title : string = 'Enthusiastic Programmer';
  updateSocialMedia : boolean = false;

  constructor() {
    // console.log( VERSION.full );
  }

  onActivate( event ) {
    if( event instanceof SpContactMeComponent ) {
      this.updateSocialMedia = true;
    } else {
      this.updateSocialMedia = false;
    }
  }
}
