import { Component, OnInit } from '@angular/core';
import { SpMainService } from '../sp-main.service';
import { SPProject } from '../sp-model';

@Component({
  selector: 'app-sp-projects',
  templateUrl: './sp-projects.component.html',
  styleUrls: ['./sp-projects.component.scss']
})
export class SpProjectsComponent implements OnInit {

  spProjects: SPProject[];
  project: SPProject;

  constructor(
    private spMainService: SpMainService,
  ) { }

  ngOnInit(): void {
    this.spMainService.getSPProjects().subscribe((data: SPProject[]) => this.spProjects = data);
  }

  displayProjectDetails(project: SPProject) {
    this.project = project;
    setTimeout(() => {
      const ele = document.getElementById('projectDetails');
      ele.scrollIntoView({ behavior: 'smooth', block: 'center' });
    }, 300);
  }

}
